.App {
  text-align: center;
}
.todo-item {
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  display: flex;
  margin: 10px 0;
  align-items: center;
  background-color: blue;
  color: white;
  padding: 10px;
  border-radius: 15px;
}
.delete-icon {
  cursor: pointer;
  border-radius: 50%;
  color: red;
  border: 1.5px solid red;
  border-radius: 50%;
  padding: 1px;
  background-color: white;
  font-size: 21px;
}
.edit-icon {
  cursor: pointer;
  border-radius: 50%;
  color: #4084de;
  border: 1.5px solid #4084de;
  padding: 1px;
  background-color: white;
  font-size: 21px;
  border-radius: 50%;
  margin: 0 10px;
}
